import React from 'react'
import { connect } from 'react-redux'
import { Redirect, useLocation } from 'react-router-dom'

import { Editor } from './layout'
import { Navigation } from '@@/containers'

import './MePage.scss'

export const MePage = (
  {
    isAuth
  }) => {
  const location = useLocation()

  return (
    <>
      <section className='mePage'>
        <div className='mePage__main'>
          <div className='mePage__main_header'>
            <Navigation />
          </div>
          <div className='mePage__main_editor'>
            <Editor/> 
          </div>
        </div>
      </section>
      {!isAuth && <Redirect to={{pathname: "/login", state: { from: location.pathname}}} />}
    </>
  )
}

const mapStateToProps = ({dialogs}) => ({
  isAuth: dialogs.isAuth
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(MePage)
