import React, { useEffect } from 'react';
import { 
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
  } from 'react-router-dom'
import isMobile from 'ismobilejs';
import { connect } from 'react-redux';

import { 
  PreviewPage, 
  ResultPage, 
  ChoosePage, 
  TestPage, 
  LoginPage, 
  MessengerPage,
  MePage

} from '@@/pages'

import './styles/index.scss';

const App = ({isMobileVersion}) => {

  useEffect(() => {
    const { pathname } = window.location;
    const t1 = isMobile(window.navigator).phone;
    const t2 = isMobileVersion
    if( t1 && !t2 ) {
      window.location.replace(pathname)
    }
  })

  return (
    <div className='pageWrapper'>
      <Router>
        <Switch>
          <Route exact path="/">
            <Redirect to="/about" />
          </Route>
          <Route exact path="/about">
            <PreviewPage />
          </Route>
          <Route exact path="/result">
            <ResultPage />
          </Route>
          <Route path="/main">
            <ChoosePage />
          </Route>
          <Route exact path="/dude/*">
            <div>{"hey dude"}</div>
          </Route>
          <Route exact path="/test">
            <TestPage />
          </Route>
          <Route exact path='/login'>
            <LoginPage />
          </Route>
          <Route exact path='/messenger'>
            <MessengerPage />
          </Route>
          <Route exact path='/me'>
            <MePage />
          </Route>
        </Switch>
      </Router>
    </div>
  )
}

export default connect(({dialogs: {isMobileVersion}}) => ({isMobileVersion}))(App) ;