import React, { useState, useRef, useEffect } from 'react'
import { connect } from 'react-redux'
import classNames from 'classnames'

import { Input, Button } from 'antd'
import { Message, DateElement } from '@@/components'

import { createMessage, readMessages, getPollMessages } from '@/actions'

import './Chat.scss'

const { TextArea } = Input

export const Chat = (
  {
  choosenDialogId,
  readyMessengesData,
  dialogs, 
  newDialogTargetId,
  v,
  isLiveUnreaded,

  createMessage,
  readMessages,
  getPollMessages
}) => {
  
  const [inputValue, setInputValue] = useState('')
  const [overflowData, setOverflowData] = useState({})

  const overflow = useRef(null)

  const dialogIsPicked = choosenDialogId !== null

  const messages = (() => {
    const res = [];
    readyMessengesData
    .forEach(({date, messages}) => {
      res.push( <DateElement date={date} />);
      messages
      .forEach(data => {
        res.push( <Message {...data} />)
      })
    });
    return res;
  })()

  useEffect(() => {
    const { scrollHeight, scrollTop, clientHeight } = overflow.current

    const keyWord = v.split('$$')[0] 
    if(keyWord === 'pickEvent') {
      overflow.current.scrollTop = scrollHeight - clientHeight;
    }
    if(keyWord === 'setPollMessages') {
      overflow.current.scrollTop = scrollHeight-overflowData.realHeight
    }

    if(keyWord === 'pickEvent' && (messages.length < 15 && messages.length >= 1)) {
      setOverflowData({realHeight: scrollHeight})
      getPollMessages()
    }
  })

  function mouseMoveHandler(ev) {
    if(isLiveUnreaded) {
      readMessages()
    }
  }

  function submitHandler () {
    if (dialogIsPicked) {
      createMessage(inputValue);
      setInputValue('');
    }
  }

  function scrollHandler() {
    const { scrollTop, scrollHeight } = overflow.current;
    if(scrollTop === 0 && (() => {
      const prevPoll = readyMessengesData?.[0]?.messages?.[0]?.prevPoll || 'none'
      return prevPoll !== 'none'
    })()) {
      setOverflowData({realHeight: scrollHeight})
      getPollMessages()
      
      // if(v.split('$$')[0] !== 'pickEvent') {
        
      // }
      
    }
  }

  
  
  return (
    <section className='chat' onMouseMove={mouseMoveHandler} >
      <div className='chat__main' ref={overflow} onScroll={scrollHandler}>
        <div className={classNames(
          'chat__main_wrapper', 
          !dialogIsPicked ? 'chat__main_wrapper-help' : null
          )}>
          { dialogIsPicked
           ? messages // [...messages, ...messages, ...messages] 
           : 'Pick dialog'
          }
        </div>
      </div>
      <div className='chat__inputArea'>
        <div className='chat__inputArea_textArea'>
          <TextArea autoSize 
            onChange={ev => setInputValue(ev.target.value)}
            value={inputValue} 
          />
        </div>
        <div className='chat__inputArea_button'>
          <Button onClick={submitHandler}>Send</Button>
        </div>
      </div>
      
    </section>
  )
}

const mapStateToProps = (
  {dialogs: 
    {
      choosenDialogId, 
      readyMessengesData, 
      dialogs, 
      newDialogTargetId,
      v,
      isLiveUnreaded,
     
    }}) => ({
  choosenDialogId, 
  readyMessengesData,
  dialogs, 
  newDialogTargetId,
  v,
  isLiveUnreaded,

})

const mapDispatchToProps = {
  createMessage, readMessages, getPollMessages
}

export default connect(mapStateToProps, mapDispatchToProps)(Chat)
