import React from 'react'
import { connect } from 'react-redux'
import { Redirect, useLocation } from 'react-router-dom'

import { Chat, ChatHeader, DialogsTab, DialogsSearch } from './layout'
import { Navigation } from '@@/containers'

import './MessangerPage.scss'

export const MessangerPage = ({isAuth}) => {
  const location = useLocation()

  return (
    <>
      <section className='messangerPage'>
        <div className='messangerPage_main'>
          <div className='messangerPage_main__header'>
            <Navigation />
          </div>
          <div className='messangerPage_main__chat'>
            <div className='messangerPage_main__chat-left'>
              <DialogsSearch />
              <DialogsTab />
            </div>
            <div className='messangerPage_main__chat-right'>
              <ChatHeader />
              <Chat />
            </div>
          </div>
        </div>
      </section>
      {!isAuth && <Redirect to={{pathname: "/login", state: { from: location.pathname}}} />}
    </>
  )
}

const mapStateToProps = ({dialogs: {isAuth}}) => ({
  isAuth
})

const mapDispatchToProps = {
  
}

export default connect(mapStateToProps, mapDispatchToProps)(MessangerPage)
